.card {
    position: relative;
    top: 0;
    transition: all 300ms linear;

    &__title {
        font-size: rem(22px);
        margin-bottom: 0.45454545454545453em;
        a {
            transition: all 300ms ease-in-out;
        }
    }

    &__button {
        margin-top: auto;
    }

    &__image {
        display: block;
        max-width: 100%;
        height: auto;

        &-wrap {
            position: relative;
            overflow: hidden;
            border-radius: 3px;
        }

        &-overlay {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            z-index: 1;
            background-color: rgba(0,0,0,0.7);
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 300ms ease-in-out;
            opacity: 1;
            visibility: visible;
        }
    }

    &:hover,
    &:focus-within {
        top: -10px;

        .card__title {
            a {
                color: #FFCB05;
            }
        }

        .card__image-overlay {
            opacity: 1;
            visibility: visible;
        }
    }

    @media (hover) {
        .card__image-overlay {
            opacity: 0;
            visibility: hidden;
        }
    }
}